'use client';

import React, { useState, useEffect } from 'react';
import { Mail, Phone, MapPin, MessageSquare, Clock, Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';

export default function ContactPage() {
    const [settings, setSettings] = useState<any>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchSettings = async () => {
            try {
                const response = await fetch('http://localhost:3333/api/settings/public');
                if (response.ok) {
                    const data = await response.json();
                    setSettings(data);
                }
            } catch (error) {
                console.error('Failed to fetch settings:', error);
            } finally {
                setLoading(false);
            }
        };

        fetchSettings();
    }, []);

    const contactInfo = {
        email: settings?.contact?.email || 'contact@sante-vitalite.com',
        phone: settings?.contact?.phone || '+225 00 00 00 00 00',
        whatsapp: settings?.contact?.whatsapp || '+225 00 00 00 00 00',
        address: settings?.business?.address || 'Abidjan, Côte d\'Ivoire',
        hours: settings?.business?.hours || 'Lun - Sam : 08:30 - 18:30',
    };

    return (
        <div className="min-h-screen bg-white">
            {/* Header */}
            <section className="py-20 border-b border-black/5 bg-black/5">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
                    <h1 className="text-5xl md:text-7xl font-bold tracking-tighter mb-6">Contactez-nous</h1>
                    <p className="text-xl text-black/60 font-light max-w-2xl mx-auto">
                        Une question sur nos produits ? Un besoin de conseil personnalisé ? Notre équipe est à votre écoute pour vous accompagner.
                    </p>
                </div>
            </section>

            {/* Main Content */}
            <section className="py-16 md:py-24">
                <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-12">

                        {/* Contact Info Column */}
                        <div className="space-y-8">
                            <h2 className="text-3xl font-bold tracking-tight">Nos Coordonnées</h2>
                            <div className="space-y-6">
                                {loading ? (
                                    <div className="flex items-center gap-2 text-black/40">
                                        <Loader2 className="w-4 h-4 animate-spin" />
                                        <span className="text-sm font-medium tracking-wide">Chargement...</span>
                                    </div>
                                ) : (
                                    <>
                                        <div className="flex items-start gap-5 p-4 rounded-3xl hover:bg-black/5 transition-colors">
                                            <div className="w-12 h-12 rounded-2xl bg-black flex items-center justify-center flex-shrink-0">
                                                <Mail className="w-5 h-5 text-white" />
                                            </div>
                                            <div>
                                                <p className="text-xs font-bold uppercase tracking-widest text-black/40 mb-1">Email</p>
                                                <p className="text-lg font-medium">{contactInfo.email}</p>
                                            </div>
                                        </div>

                                        <div className="flex items-start gap-5 p-4 rounded-3xl hover:bg-black/5 transition-colors">
                                            <div className="w-12 h-12 rounded-2xl bg-black flex items-center justify-center flex-shrink-0">
                                                <Phone className="w-5 h-5 text-white" />
                                            </div>
                                            <div>
                                                <p className="text-xs font-bold uppercase tracking-widest text-black/40 mb-1">Téléphone</p>
                                                <p className="text-lg font-medium">{contactInfo.phone}</p>
                                            </div>
                                        </div>

                                        <div className="flex items-start gap-5 p-4 rounded-3xl hover:bg-black/5 transition-colors">
                                            <div className="w-12 h-12 rounded-2xl bg-black flex items-center justify-center flex-shrink-0">
                                                <MapPin className="w-5 h-5 text-white" />
                                            </div>
                                            <div>
                                                <p className="text-xs font-bold uppercase tracking-widest text-black/40 mb-1">Adresse</p>
                                                <p className="text-lg font-medium">{contactInfo.address}</p>
                                            </div>
                                        </div>

                                        <div className="flex items-start gap-5 p-4 rounded-3xl hover:bg-black/5 transition-colors">
                                            <div className="w-12 h-12 rounded-2xl bg-black flex items-center justify-center flex-shrink-0">
                                                <Clock className="w-5 h-5 text-white" />
                                            </div>
                                            <div>
                                                <p className="text-xs font-bold uppercase tracking-widest text-black/40 mb-1">Horaires</p>
                                                <p className="text-lg font-medium">{contactInfo.hours}</p>
                                            </div>
                                        </div>
                                    </>
                                )}
                            </div>
                        </div>

                        {/* Direct Support Column */}
                        <div className="flex flex-col justify-center">
                            <div className="p-10 bg-black rounded-[3rem] text-white relative overflow-hidden group">
                                <div className="absolute top-0 right-0 w-64 h-64 bg-white/5 rounded-full blur-[80px] group-hover:bg-white/10 transition-colors"></div>
                                <div className="relative z-10 space-y-6">
                                    <div className="w-14 h-14 rounded-2xl bg-white/10 flex items-center justify-center">
                                        <MessageSquare className="w-7 h-7 text-white" />
                                    </div>
                                    <h3 className="text-3xl font-bold leading-tight">Besoin d'aide immédiate ?</h3>
                                    <p className="text-white/70 text-lg font-light leading-relaxed">
                                        Nos experts sont disponibles pour répondre à toutes vos questions et vous guider dans le choix de votre protocole.
                                    </p>
                                    <a
                                        href={`https://wa.me/${contactInfo.whatsapp.replace(/[^\d]/g, '')}`}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                        className="block"
                                    >
                                        <Button className="w-full bg-white text-black hover:bg-white/90 rounded-2xl h-14 text-lg font-bold">
                                            Discuter sur WhatsApp
                                        </Button>
                                    </a>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </section>
        </div>
    );
}
